import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

import Unsplash, { toJson } from 'unsplash-js';

class App extends Component {

  unsplash = new Unsplash({
    applicationId: "4a098422636131849a16025acc29c09de726d2d107f4d665a67b1bc617fd6b91",
    secret: "cc7b90fc8f2bfc4664b7cc3a72ad02d6238f6d205ddf16f5fb2cfb32e7b9b462",
  });

  state = {
    photoUrl: '',
  };

  // ;;life ----------------------------------------------------------------------------------------

  componentDidMount() {
    localStorage.getItem('photoUrlCashed');
    console.log(navigator.serviceWorker.state);
  }

  // ;;inner ---------------------------------------------------------------------------------------

  requestToUnsplash = () => {
    this.unsplash.photos.getRandomPhoto()
      .then(toJson)
      .then(json => {
        this.setState({ photoUrl: json.urls.regular });
      });
  };

  // ;;render --------------------------------------------------------------------------------------

  // renderImg() {
  //   return <img src={} />
  // }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
        </header>
        <button
          onClick={this.requestToUnsplash}
          type="button">button</button>
        <img src={this.state.photoUrl} alt="" />
      </div>
    );
  }
}

export default App;



